// Utilities
@import "utility/siteVars";
// Base Imports
@import 'pages/layoutDivs';
@import 'pages/welcome';
//Button Mixins
@import 'mixins/baseButton';
@import 'mixins/colorButton';
// Div Mixins
@import 'mixins/menuDiv';
@import 'mixins/profileDiv';
@import 'mixins/sideInner';
// Textfield Mixins
@import 'mixins/createTextField';

html{
  background-image: url($imgBackground1);
  background-attachment: fixed;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  min-width: 1000px;
  background-color: black;
}
//Containers:
#container
{
  text-align: center;
  position: absolute;
  height: 100%;
  width:100%;
}
#leftContainer
{
  opacity: .9;
  transition: opacity 1s;
}
#rightContainer
{
  opacity: .9;
  transition: opacity 1s;
}

table {
  border-collapse: separate;
  border-spacing: 3px;

}
table, td {
  border: 1px solid black;
  border-radius: 7px;
  -moz-border-radius: 7px;
  padding: 3px;
}

/* Div specific */
//Flash Messages:
.flash-info
{
  color: #009900;
}
.flash-success
{
  color: #009900;
}
.flash-danger
{
  color: red;
}


//Menus:
#topLeftMenu
{
  @include menuDiv($topBackgroundColor);
  left: $sideMargin;
  top: $topPosition;
}
#bottomLeftMenu
{
  @include menuDiv($bottomBackgroundColor);
  left: $sideMargin;
  bottom: $bottomPosition;

}
#topRightMenu
{
  @include menuDiv($topBackgroundColor);
  right: $sideMargin;
  top: $topPosition;

}
#bottomRightMenu
{
  @include menuDiv($bottomBackgroundColor);
  right: $sideMargin;
  bottom: $bottomPosition;
}
//Profiles
#leftProfile
{
  @include profileDiv($profileBackgroundColor);
  left: 0;
  width: 24%;
}
#rightProfile
{
  @include profileDiv($profileBackgroundColor);
  right: 0;
  width: 24%;
}
.innerSideMenus
{
  @include sideInner();
}

//Content
#centerContent
{
  width: 50%;
  height: auto;
  background-color: $centerBackgroundColor;
  opacity: $centerOpacity;
  border: 2px solid;
  border-radius: 25px;
  font-family: Verdana, Geneva, sans-serif;
  z-index: 2;
  margin: 132px auto;
  min-width: 300px;
}
#centerMenu
{
  margin: 0 auto;
  width: 90%;
  height: auto;
}
#centerText
{
  margin: 0 auto;
  width: 95%;
  height: auto;
}
#centerTextContent
{
  text-align: left;
  font-size: 103%;
}
#createOptions
{
  margin: 0 auto;
  padding: 3px;
}


.createSubmit
{
  margin: 7px auto;
  width: 20%;
  background-color: $bottomBackgroundColor;
  border: 1px solid;
  border-radius: 20px;
  opacity: $centerOpacity;
}

//Errors:
.errors
{
  background-color: red;
}
//TextField for posts, extensions, drafts
#createBodyText
{
  @include baseTextField();
  font-size: 125%;
}

#centerFooter
{

  margin: 0 auto;
  width: 50%;
  height: auto;
  background-color: #202020;
  opacity: $centerOpacity;
  border: 2px solid;
  border-radius: 25px;
  color: #ffffff;
}

/* Button specific */
.navButton
{
  @include baseButton;
  @include colorNavButton($navButtonColor1, $navButtonColor2, $navTextColor);
  &:hover {
    color: $navTextHoverColor;
    background: -webkit-linear-gradient($navHoverColor1, $navHoverColor2);
    background: -o-linear-gradient($navHoverColor1, $navHoverColor2);
    background: -moz-linear-gradient($navHoverColor1, $navHoverColor2);
    background: linear-gradient($navHoverColor1, $navHoverColor2);
  }
}
.interactButton {
  @include baseButton;
  @include colorActButton($actButtonColor1, $actButtonColor2, $actTextColor);
  &:hover {
    color: $actTextHoverColor;
    background: -webkit-linear-gradient($actHoverColor1, $actHoverColor2);
    background: -o-linear-gradient($actHoverColor1, $actHoverColor2);
    background: -moz-linear-gradient($actHoverColor1, $actHoverColor2);
    background: linear-gradient($actHoverColor1, $actHoverColor2);
  }
}
h2
{
  text-decoration: underline;
}
